.Shadow-b-Lifted, .Shadow-b-Lifted-Spotlight {
  position: relative;
}

.Shadow-b-Lifted:before, .Shadow-b-Lifted:after, .Shadow-b-Lifted-Spotlight:before, .Shadow-b-Lifted-Spotlight:after {
  z-index: -1;
  position: absolute;
  content: "";
  left: 10px;
  width: 50%;
  top: 80%;
  max-width: 300px;
  background: #777;
}

.Shadow-b-Lifted:before, .Shadow-b-Lifted:after {
  bottom: 15px;
  box-shadow: 0 15px 10px #777;
  transform: rotate(-3deg);
}

.Shadow-b-Lifted:after {
  right: 10px;
  left: auto;
  transform: rotate(3deg);
}

.Shadow-b-Lifted-Spotlight:before, .Shadow-b-Lifted-Spotlight:after {
  bottom: 25px;
  box-shadow: 0 35px 20px #777;
  transform: rotate(-8deg);
}

.Shadow-b-Lifted-Spotlight:after {
  transform: rotate(8deg);
  right: 10px;
  left: auto;
}

.Shadow-br-Lifted, .Shadow-bl-Lifted {
  position: relative;
}

.Shadow-br-Lifted:after, .Shadow-bl-Lifted:after {
  z-index: -1;
  position: absolute;
  content: "";
  bottom: 15px;
  width: 50%;
  top: 80%;
  max-width: 300px;
  background: #777;
  box-shadow: 0 15px 10px #777;
  transform: rotate(3deg);
}

.Shadow-br-Lifted:after {
  right: 10px;
  left: auto;
}

.Shadow-bl-Lifted:after {
  left: 10px;
}

